import React from 'react';
import { Progress, Space, Button } from 'antd';

function Next(props){
  const vote = () =>{
    props.setMode(1);
  }
  return(
    <>
    <h3>{props.now}번님! 투표를 하시려면 버튼을 눌려주세요</h3>
  <Space wrap>
    <Progress type="circle" percent={parseInt((props.now-1)/props.member *100)} />
  </Space>
  <div> 
    <Button type="primary" key="console" onClick={vote}>
        투표하러가기
      </Button>
      </div>
 
    </>
  )
  };

export default Next;