import React from 'react';
import { Button, Space, Progress } from 'antd';

function Result(props){
    const reset = () =>{
        props.setNow(1);
        props.setChoose(0);
        props.setMode(0);
      }

  return(
    <>
    <h1>결과 : {props.choose}명</h1>
    <Space wrap>
    <Progress type="circle" percent={parseInt((props.choose)/props.member *100)} />
  </Space>
<div>
<Button onClick={reset} type="primary" key="console">
        다시하기
      </Button>
</div>
    </>
  )
  };

export default Result;