function Title(props) {
  const changeMode = () =>{
    console.log("click");
    props.setMode(2);
  }

  const increseMember = () =>{
    console.log("증가");
    props.setMember(props.member+1);
  }

  const decreseMember = () =>{
    console.log("증가");
    if(props.member <=2) return;
    props.setMember(props.member-1);
  }
    return (
      <>
    <h1>휴지게임</h1>
    <h2>참여인원을 설정해주세요</h2>
    <img onClick={decreseMember} src="https://cdn.pixabay.com/photo/2016/09/05/10/50/app-1646213_1280.png" style={{width : "100px"}} alt="<" /> 
    <span style={{fontSize : "100px" }}>{props.member}</span>
    <img onClick={increseMember} src="https://cdn.pixabay.com/photo/2016/09/05/10/50/app-1646214_1280.png" style={{width : "100px"}} alt=">" />

    <div>
    <img onClick={changeMode} src="https://t3.ftcdn.net/jpg/00/86/65/98/360_F_86659811_QtkmS52Tr5rKBqvVzwTkuHw2tt5yLHMc.jpg" alt="게임시작" style={{width : "200px"}}/>
    </div> 
      </>
  
    );
  }
  
  export default Title;

  
  