function Select(props) {

  const O = () =>{
    props.setChoose(props.choose+1);
    props.setNow(props.now+1);
    if(props.now  === props.member){
      props.setMode(3)
      return
    }
    props.setMode(2);
  }
  const X = () =>{
    props.setNow(props.now+1);
    if(props.now === props.member){
      props.setMode(3)
      return
    }
    props.setMode(2);
  }
    return (
      <>
      <h3>질문에 해당하면 O를 해당하지않으면 X를 눌려주세요</h3>
      <table border={1}>
      <tr>
        <td><img src="https://cdn.pixabay.com/photo/2016/09/12/09/51/abc-1663384_1280.png" onClick={O} style={{width : "200px"}} alt="O" /></td>
        <td><img src="https://cdn.pixabay.com/photo/2016/09/12/09/58/abc-1663423_1280.png" onClick={X} style={{width : "200px"}} alt="X" /></td>
        </tr>
      </table>
      </>
  
    );
  }
  
  export default Select;
  